.file-input::before {
  content: "Imagen";
  display: inline-block;
  background: #fff;
  color: #bcbcbc;
  outline: none;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 700;
  text-align: center;
  -webkit-transition: 0.3s all ease;
  transition: 0.3s all ease;
  border-radius: 30px;
  box-shadow: 0px 2px 33px 0px rgba(0, 0, 0, 0.1);
  -webkit-animation: fadein 5s;
  animation: fadein 5s;
  cursor: pointer;
  border: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 14px 38px;
  white-space: nowrap;
  -webkit-user-select: none;
  cursor: pointer;
  text-shadow: 1px 1px #fff;
  font-weight: 700;
  font-size: 10pt;
  visibility: visible;
  position: relative;
  width: fit-content;
  animation: fadein 5s;
  cursor: pointer;
}
.file-input:hover::before {
  opacity: 1 !important;
  box-shadow: 0px 0px 5px #ecb84c !important;
}
.file-input:active::before {
  outline: none !important;
  box-shadow: 1px 2px 7px #ecb84c !important;

  -webkit-animation: 1s;

          animation: 1s;
}
