.limitHeight {
  height: 30rem;
  -webkit-transition: 1s;
  transition: 1s;
  overflow: hidden;
  -webkit-animation: heightEfect;
          animation: heightEfect;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  border-radius: 15px;
}
@-webkit-keyframes heightEfect {
  0% {
    height: 100%;
  }
  100% {
    height: 30rem;
  }
}
@keyframes heightEfect {
  0% {
    height: 100%;
  }
  100% {
    height: 30rem;
  }
}
.noLimitHeight {
  height: 100%;
  -webkit-transition: 1s;
  transition: 1s;
  overflow: hidden;
  -webkit-animation: heightEfect;
          animation: heightEfect;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}
